}

.loader {
  perspective: 1000px;
  width: 56px;
  height: 56px;
  color: #fff;
  border-radius: 50%;
  transition-duration: .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotateZ(45deg);
}

