  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: var(--clr-hover-primary);
  animation-delay: .4s;
  transform: rotateY(70deg);
}

.visually-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.breed-select {
  background-color: var(--clr-background-secondary);
  color: var(--clr-hover-primary);
  width: 230px;
  transition: transform var(--ss-animation-timing), opacity var(--ss-animation-timing);
  border: none;
  border-radius: 10px;
  margin: 20px;
  padding: 16px 10px;
}

