  background-color: var(--clr-select);
  color: var(--clr-hover-primary);
}

::-webkit-scrollbar {
  display: none;
}

.cat-info {
  background-color: var(--clr-background-secondary);
  border-radius: 10px;
  margin: 20px;
  display: flex;
}

